<template>
  <div class="page">
    <mt-header class="bar-nav" title="自动投标">
      <mt-button slot="left" icon="back" v-back-link></mt-button>
    </mt-header>
    <div class="auto-top">
      <p>您的可用余额</p>
      <p>{{resdata.userMoney | currency('',2)}}元</p>
    </div>
    <mt-cell title="自动投标状态" class="margin-t-15">
      <mt-switch v-model="switch_value" @change="handleChange"></mt-switch>
    </mt-cell>
    <router-link :to="autoLink">
    <div class="form-text aui-border-t" v-show="switch_value">
        <label>投标参数详情</label>
        <img src="../../../assets/images/public/arrow_right.png" class="arrow-right">
    </div>
    </router-link>
    <div class="form-prompt margin-t-25">
      <div class="prompt-title">温馨提示：</div>
      <p class="margin-t-15">1、账户可用余额必须大于等于X元可启动自动投资功能；</p>
      <p>2、自动投资不可投资新客专享产品、定向产品、变现产品，转让产品；</p>
      <p>3、自动投资不可使用红包、加息券优惠；</p>
      <p>4、开启该功能后将在5分钟内生效。</p>
    </div>
  </div>
</template>
<script>
  import * as ajaxUrl from '../../../ajax.config'
 export default {
   data(){
     return {
       switch_value: false,
       resdata: '',
       autoLink: '/account/auto/setting'
     }
   },
   created(){
     this.$indicator.open({spinnerType: 'fading-circle'}) //提示初始化加载
     let getParams = {
       userId: this.$store.state.user.userId,
       __sid: this.$store.state.user.__sid
     }
     this.$http.get(ajaxUrl.autoInit, {params: getParams}).then((res) => {
       this.resdata = res.data.resData
       //判断是否开启了自动投标
       if(this.resdata.isAuto == 1){
         this.switch_value = true;
         this.autoLink = '/account/auto/modify'
       }
       this.$indicator.close() // 关闭提示
     })
   },
   mounted(){
     // 清除边框
     document.getElementsByClassName('mint-cell-wrapper')[0].style.backgroundImage = 'none'
   },
   methods: {
     handleChange(flag) {
       this.switch_value = flag;
     }
   }
 }
</script>

<style scoped>
  .auto-top{
    height: .8rem;
    width: 100%;
    background: #F95A28;
    padding: 0 5%;
  }
  .auto-top p{
    margin-top: .16rem;
    float: left;
    width: 100%;
    line-height: 1;
    font-size: .16rem;
    font-family: arial;
    color: #fff;
  }
  .mint-cell-text{
    font-size: .16rem;
  }
  .form-text{  height: .48rem;  }
  .form-text label{
    line-height: .48rem;
  }
  .arrow-right{
    height: .14rem;
    float: right;
    margin-top: .17rem;
  }
  .form-prompt p{
    line-height: .24rem;
  }
</style>




